.card {
    position: relative;
    height: 340px;
    border-radius: 10px;
}

.card img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;
}
.card .overlay {
    position: absolute;
    background-image: linear-gradient(to bottom, #ffffff00, #00000050); 
    height: 100%;
    width: 100%;
    border-radius: 10px;
    z-index: 1;
}

.card span {
    position: absolute;
    color: #FFF;
    bottom: 13px;
    left: 13px;
    font-size: 30px;
    font-weight: 500;
    z-index: 2;
}